@import "master";
@import "fonts";

* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
}
body {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  font-family: "TT Norms", sans-serif;
  color: $gray;
}
.main {
  flex: 1 0 auto;
}
.footer {
  flex-shrink: 0;
}
img {
  width: 100%;
}

a {
  color: $blue;
}

.logo {
  &-wrapper {
    display: flex;
    align-items: center;
    transition: 0.5s;
    text-decoration: none !important;
    &:hover {
      opacity: 0.8;
    }
  }
  &-text {
    color: $blueBlack;
    font-size: 1.5em;
    font-weight: bold;
    &--negative {
      color: #fff;
    }
  }
  width: 32px;
}

.main-header {
  .icon-menu {
    color: #fff;
  }
}
.navbar-inner {
  width: 100%;
  &__top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5em 0;
  }
  &__bottom {
    background: $green;
    background: linear-gradient(
      225deg,
      rgba(20, 211, 149, 1) 0%,
      rgba(103, 232, 139, 1) 100%
    );
    padding: 1.375em;
    font-weight: 500;
    width: 100%;
  }

  &-mobile {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    .navbar-toggler {
      padding: 15px;
      background-color: #1a1e2e;
    }
  }
}
.navbar-contacts {
  display: flex;
  align-items: center;
  &__item {
    &:not(:last-child) {
      margin-right: 3.625em;
    }
    &-title {
      color: $blueBlack;
      font-size: 0.75em;
      margin: 0;
    }
    &-content {
      font-size: 0.875em;
      font-weight: 500;
      margin: 0;
      text-decoration: none;
      color: $black !important;
    }
  }
}
.nav-link {
  color: #fff;
  font-size: 0.9375em;
  transition: 0.5s;
  &:hover {
    color: #fff;
    opacity: 0.8;
  }
}

.page-title {
  font-size: 1.5em;
  font-weight: 300;
  border-bottom: 1px dashed $black;
  display: inline-block;
}
.section {
  &-title {
    font-size: 2.75em;
    font-weight: bold;
    color: $black;
    &--light {
      color: #fff;
    }
  }
  &-subtitle {
    font-size: 1.5em;
    font-weight: 300;
    max-width: 620px;
    display: inline-block;
    color: $black;
    &--light {
      color: $lightGrayDark;
    }
  }
}

.dd-button {
  padding: 12px 32px;
  color: #fff !important;
  background: $green !important;
  background: linear-gradient(
    225deg,
    rgba(20, 211, 149, 1) 0%,
    rgba(103, 232, 139, 1) 100%
  ) !important;
  outline: none !important;
  border: none;
  text-decoration: none !important;
  border-radius: 5px;
  transition: 0.5s;
  &:hover {
    opacity: 0.8;
  }

  &-dashed {
    padding: 10px 32px;
    color: $green !important;
    border: 1px dashed #dce0e5;
    border-radius: 5px;
    outline: none !important;
    background: none;
    transition: 0.5s;
    &--dark {
      border-color: #2b3147;
    }
    &:hover {
      opacity: 0.8;
    }
  }
}

section {
  padding: 120px 0;
}

.hero-section {
  min-height: 750px;
  background: #fff;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(248, 251, 255, 1) 100%
  );
  background-image: url(../assets/hero-bg.png);
  background-repeat: no-repeat;
  // background-size: cover;
  background-size: 100% 100%;
  background-position: center -54px;
  padding: 80px 0;

  &__icon {
    &-wrapper {
      color: $blueBlack;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    font-size: 40px;
    margin-right: 1rem;

    &-text {
      margin: 0;
    }
  }
}

.contact-form {
  background-color: #fff;
  padding: 2em;
  border-radius: 6px;
  box-shadow: 0 25px 35px 0 rgba(0, 0, 0, 0.08);
  margin-bottom: 4.375em;
  &__title {
    font-size: 1.125em;
    font-weight: 500;
  }
  &__input {
    padding: 14px 24px;
    height: 100%;
  }
  .custom-control-input:checked ~ .custom-control-label::before {
    border-color: $blue !important;
    background-color: $blue !important;
  }
  .custom-checkbox {
    label {
      &::before {
        top: 0.15rem !important;
        left: -1.6rem !important;
        width: 1.25rem !important;
        height: 1.25rem !important;
      }
      &::after {
        transform: scale(1.25) !important;
      }
    }
  }

  &__response {
    &-error {
      font-size: 0.85em;
      text-align: center;
      color: #dc3545;
      &:not(.active) {
        display: none;
      }
    }
    &-success {
      font-size: 0.85em;
      text-align: center;
      color: $green;
      &:not(.active) {
        display: none;
      }
    }
  }
}

.features-section {
  background: #fff;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(248, 251, 255, 1) 100%
  );
}
.feature {
  width: 100%;
  background-color: #fff;
  padding: 1.5em;
  border-radius: 6px;
  box-shadow: 0px 25px 35px rgba(0, 0, 0, 0.02);
  margin-bottom: 2em;
  &__icon {
    font-size: 1.75em;
    color: $blue;
  }
}

.video-section {
  padding: 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(248, 251, 255, 1) 100%
  );
  &__content {
    background-color: $green;
    // background-image: url(../assets/video-section-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    text-align: center;
    color: #fff;
    padding-top: 40px;
    padding-bottom: 120px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    &-icon {
      font-size: 90px;
    }
    &-btn {
      display: flex;
      margin: auto;
      justify-content: center;
      align-items: center;
      padding: 1em 2em;
      background: none;
      border: 1px dashed #fff;
      border-radius: 5px;
      color: #fff;
      font-weight: bold;
      transition: 0.5s;
      outline: none !important;
      i {
        margin-left: 8px;
        font-size: 1.5em;
      }

      &:hover {
        opacity: 0.8;
      }
    }
  }
}

.prices-section {
  // background-color: #0f1323;
  // background-image: url(../assets/prices-background.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  margin-top: -50px;
}

.prices-table {
  background-color: #161929;
  padding: 40px 48px;
  border-radius: 5px;
  &__header {
    color: #fff;
    font-size: 1.125em;
  }
  &__content {
    color: $lightGrayDark;
  }
  &__btn {
    background-color: rgba(20, 211, 150, 0.05) !important;
    color: $green !important;
    font-weight: bold;
    border: none !important;
  }
}

.specs-section {
  background-color: #fff;
  // background-image: url(../assets/specs-background.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center left;
}
.specs-table {
  max-width: 770px;
  margin: auto;
  background: #fff;
  padding: 15px 24px;
  border-radius: 5px;
  .row {
    &:nth-child(even) {
      background: #fcfdff;
    }
    [class*="col"] {
      p {
        margin: 0;
        padding: 1em 0;
      }
      &:first-child {
        color: $black;
      }
    }
  }
}

.individual-orders-section {
  background-color: #fff;
}

.individual-order-feature {
  display: flex;
  &__icon {
    font-size: 4em;
    color: $blue;
  }
  &__title {
    font-size: 1.375em;
    color: $black;
    font-weight: bold;
  }
}

.how-to-order-section {
  background: #fff;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(248, 251, 255, 1) 100%
  );
}

.how-to-order-step {
  width: 100%;
  background-color: #fff;
  padding: 1.5em;
  border-radius: 6px;
  box-shadow: 0px 25px 35px rgba(0, 0, 0, 0.02);
  margin-bottom: 2em;
  &__number {
    font-size: 1.75em;
  }
}

.about-section {
  background-color: #0f1323;
  // background-image: url(../assets/about-background.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  &__text {
    color: $lightGrayDark;
    max-width: 770px;
    margin: auto;
  }
}
.requisite-card {
  padding: 3em 5em;
  background-color: #161929;
  box-shadow: 0px 35px 55px rgba(4, 5, 34, 0.25);
  border-radius: 5px;
  width: 100%;
  margin-bottom: 2em;
  &__title {
    color: #fff;
    font-size: 2.375em;
  }
  &__link {
    font-size: 1.125em;
  }
  &__item {
    &-title {
      font-size: 0.875em;
      margin: 0;
    }
    &-text {
      font-size: 1.125em;
      color: #fff;
    }
  }
}

.read-more-btn {
  border: none;
  background: none;
  cursor: pointer;
  font-size: 0.875em;
  font-weight: bold;
  color: $blue;
  outline: none !important;
  &__text {
    border-bottom: 1px dashed $blue;
  }
  &.activated {
    display: none !important;
  }
}

.branches-section {
  background: #fff;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(248, 251, 255, 1) 100%
  );
}

.branches-search {
  &-wrapper {
    position: relative;
  }
  box-shadow: 0px 25px 35px rgba(0, 0, 0, 0.02);
  background: #ffffff;
  border: 1px solid #f2f2f2;
  border-radius: 100px;
  position: relative;
  padding: 30px;
  outline: none !important;
  &:focus {
    border-color: $green;
    box-shadow: 0 0 0 0.2rem rgba($green, 0.25);
  }

  &__icon {
    position: absolute;
    right: 30px;
    top: 20px;
    pointer-events: none;
    color: $green;
  }
}

.branches-table {
  background-color: #fff;
  padding: 40px 48px;
  border-radius: 5px;
  &__header {
    color: $black;
    font-size: 1.125em;
  }
  &__content {
    color: $gray;
  }
  &__btn {
    background-color: rgba(20, 211, 150, 0.05) !important;
    color: $green !important;
    font-weight: bold;
    border: none !important;
  }
}

.main-footer {
  background-color: #0f1323;
  padding: 56px 0;
  &__contact {
    &-content {
      color: #fff !important;
      font-size: 1.125em;
    }
    &-title {
      font-size: 0.75em;
    }
  }
}

.social-links {
  a {
    &:not(:last-child) {
      margin-right: 1em;
    }
    &:hover {
      text-decoration: none !important;
    }
  }
  &__icon {
    font-size: 1em;
    display: inline-block;
  }
}

.cdk-overlay {
  display: none;
  &.active {
    display: flex;
  }
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.32);
  justify-content: center;
  align-items: center;
  &__close {
    position: fixed;
    top: 25px;
    right: 2.5%;
    i {
      font-size: 3em;
      color: #fff;
    }
  }
  &__content {
    width: 100%;
    max-width: 980px;
    padding: 15px;
  }
}

.video-rounded {
  border-radius: 12px;
}

.mb-6 {
  margin-bottom: 5rem;
}
