@font-face {
  font-family: "TT Norms";
  font-style: normal;
  font-weight: 400;
  src: local("TT Norms Regular"), url("../fonts/TTNorms/TTNorms-Regular.eot"),
    /* IE9 Compat Modes */ url("../fonts/TTNorms/TTNorms-Regular.woff")
      format("woff"),
    /* Modern Browsers */ url("../fonts/TTNorms/TTNorms-Regular.ttf")
      format("truetype"); /* Safari, Android, iOS */
  font-display: swap;
}
